import * as React from "react";

export type GalleryImage = { src: string; alt: string };

const TILE_IMAGES: GalleryImage[] = [
  { src: "https://source.unsplash.com/featured/500x500/?porcelain%20tile,floor", alt: "Porcelain floor tile in living area" },
  { src: "https://source.unsplash.com/featured/500x500/?ceramic%20tile,bathroom", alt: "Ceramic wall tile in modern bathroom" },
  { src: "https://source.unsplash.com/featured/500x500/?subway%20tile,kitchen", alt: "White subway tile kitchen backsplash" },
  { src: "https://source.unsplash.com/featured/500x500/?hexagon%20tile", alt: "Matte hexagon mosaic tile" },
  { src: "https://source.unsplash.com/featured/500x500/?marble%20tile", alt: "Polished marble-look porcelain tile" },
  { src: "https://source.unsplash.com/featured/500x500/?herringbone%20tile", alt: "Herringbone tile pattern in entryway" },
  { src: "https://source.unsplash.com/featured/500x500/?mosaic%20tile,shower", alt: "Shower with mosaic floor tile" },
  { src: "https://source.unsplash.com/featured/500x500/?large%20format%20tile", alt: "Large-format tile in open-plan space" },
];

const TileGallerySection: React.FC<{ images?: GalleryImage[]; city?: string }> = ({
  images = TILE_IMAGES,
  city = "Boise",
}) => {
  const [isNarrow, setIsNarrow] = React.useState(false);
  const [active, setActive] = React.useState<number | null>(null);

  React.useEffect(() => {
    const onResize = () => setIsNarrow(window.innerWidth <= 980);
    onResize();
    window.addEventListener("resize", onResize);
    return () => window.removeEventListener("resize", onResize);
  }, []);

  React.useEffect(() => {
    const onKey = (e: KeyboardEvent) => {
      if (!active && active !== 0) return;
      if (e.key === "Escape") setActive(null);
      if (e.key === "ArrowRight") setActive((i) => (i === null ? 0 : (i + 1) % images.length));
      if (e.key === "ArrowLeft") setActive((i) => (i === null ? 0 : (i - 1 + images.length) % images.length));
    };
    document.addEventListener("keydown", onKey);
    return () => document.removeEventListener("keydown", onKey);
  }, [active, images.length]);

  const styles = {
    section: {
      background: "#f7f9fb",
      padding: isNarrow ? "40px 16px" : "64px 24px",
    } as React.CSSProperties,
    wrap: { maxWidth: 1200, margin: "0 auto" },
    headerRow: {
      display: "flex",
      alignItems: isNarrow ? "flex-start" : "center",
      justifyContent: "space-between",
      gap: 16,
      marginBottom: 18,
      flexDirection: isNarrow ? ("column" as const) : ("row" as const),
    },
    eyebrow: {
      letterSpacing: ".08em",
      textTransform: "uppercase" as const,
      fontSize: 12,
      color: "#8E9595",
      fontWeight: 700,
      marginBottom: 6,
    },
    title: {
      margin: 0,
      fontSize: "clamp(22px, 3.6vw, 32px)",
      color: "#36434A",
      lineHeight: 1.2,
      fontWeight: 800,
    },
    subtitle: {
      margin: "6px 0 0",
      color: "#5a6a72",
      fontSize: isNarrow ? 14 : 15,
      maxWidth: 720,
    },
    cta: {
      display: "inline-block",
      background: "#36434A",
      color: "#f8f9fa",
      padding: "10px 16px",
      borderRadius: 999,
      textDecoration: "none",
      fontWeight: 700,
      boxShadow: "0 8px 20px rgba(0,0,0,0.12)",
      whiteSpace: "nowrap" as const,
    },
    // 4 columns on desktop, 2 on narrow
    grid: {
      display: "grid",
      gap: isNarrow ? 10 : 16,
      gridTemplateColumns: isNarrow ? "repeat(2, 1fr)" : "repeat(4, 1fr)",
    } as React.CSSProperties,
    itemBtn: {
      position: "relative" as const,
      display: "block",
      padding: 0,
      margin: 0,
      border: 0,
      background: "transparent",
      cursor: "pointer",
      borderRadius: 12,
      overflow: "hidden",
      boxShadow: "0 8px 22px rgba(0,0,0,0.12)",
      transition: "transform .2s ease, box-shadow .2s ease",
    },
    img: {
      width: "100%",
      height: 220,
      objectFit: "cover" as const,
      display: "block",
      transform: "scale(1)",
      transition: "transform .4s cubic-bezier(0.25,0.8,0.25,1)",
    },
    chip: {
      position: "absolute" as const,
      left: 12,
      top: 12,
      background: "rgba(54,67,74,0.9)",
      color: "#f8f9fa",
      padding: "6px 10px",
      fontSize: 12,
      borderRadius: 999,
      border: "1px solid rgba(255,255,255,0.25)",
      pointerEvents: "none" as const,
    },
    lightbox: {
      position: "fixed" as const,
      inset: 0,
      background: "rgba(0,0,0,0.85)",
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
      padding: isNarrow ? 12 : 24,
      zIndex: 9999,
    },
    lightboxImg: {
      maxWidth: "100%",
      maxHeight: "84vh",
      borderRadius: 12,
      boxShadow: "0 20px 60px rgba(0,0,0,0.4)",
    },
    close: {
      position: "absolute" as const,
      top: 16,
      right: 16,
      background: "rgba(255,255,255,0.14)",
      color: "#f8f9fa",
      border: "1px solid rgba(255,255,255,0.35)",
      borderRadius: 999,
      padding: "8px 12px",
      fontWeight: 700,
      cursor: "pointer",
    },
    arrows: { position: "absolute" as const, bottom: 16, display: "flex", gap: 8 },
    arrowBtn: {
      background: "rgba(255,255,255,0.14)",
      color: "#f8f9fa",
      border: "1px solid rgba(255,255,255,0.35)",
      borderRadius: 999,
      padding: "8px 12px",
      fontWeight: 700,
      cursor: "pointer",
    },
  };

  return (
    <section style={styles.section} aria-labelledby="tile-gallery-title">
      <div style={styles.wrap}>
        <div style={styles.headerRow}>
          <div>
            <div style={styles.eyebrow}>Our Work</div>
            <h2 id="tile-gallery-title" style={styles.title}>
              Recent Tile Projects in {city}
            </h2>
            <p style={styles.subtitle}>
              Ceramic, porcelain, mosaic, and marble-look installations—precise layouts, tight grout
              lines, and clean transitions.
            </p>
          </div>
          <a href="/gallery" style={styles.cta}>View Full Gallery</a>
        </div>

        <div style={styles.grid}>
          {images.map((img, i) => (
            <button
              key={i}
              style={styles.itemBtn}
              onClick={() => setActive(i)}
              aria-label={`Open larger view: ${img.alt}`}
              onMouseEnter={(e) => {
                const el = e.currentTarget.querySelector("img") as HTMLImageElement | null;
                if (el) el.style.transform = "scale(1.04)";
              }}
              onMouseLeave={(e) => {
                const el = e.currentTarget.querySelector("img") as HTMLImageElement | null;
                if (el) el.style.transform = "scale(1)";
              }}
            >
              <img src={img.src} alt={img.alt} loading="lazy" style={styles.img} />
              <span style={styles.chip}>Tile</span>
            </button>
          ))}
        </div>
      </div>

      {(active || active === 0) && (
        <div role="dialog" aria-modal="true" style={styles.lightbox} onClick={() => setActive(null)}>
          <img
            src={images[active].src}
            alt={images[active].alt}
            style={styles.lightboxImg}
            onClick={(e) => e.stopPropagation()}
          />
          <button aria-label="Close gallery" style={styles.close} onClick={() => setActive(null)}>
            ✕ Close
          </button>
          <div style={{ ...styles.arrows, left: "50%", transform: "translateX(-50%)" }}>
            <button
              aria-label="Previous image"
              style={styles.arrowBtn}
              onClick={(e) => {
                e.stopPropagation();
                setActive((i) => (i === null ? 0 : (i - 1 + images.length) % images.length));
              }}
            >
              ← Prev
            </button>
            <button
              aria-label="Next image"
              style={styles.arrowBtn}
              onClick={(e) => {
                e.stopPropagation();
                setActive((i) => (i === null ? 0 : (i + 1) % images.length));
              }}
            >
              Next →
            </button>
          </div>
        </div>
      )}
    </section>
  );
};

export default TileGallerySection;
